.item {
  font-family: "Montserrat", sans-serif;
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.233);
  margin: 0 7.5px 15px;

  &.large {
    height: 380px;
  }
  &:hover {
    cursor: pointer;
    & .background-image {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
    & .content {
      opacity: 0.9;
    }
  }
  .background-image {
    height: 100%;
    width: 100%;
    background-position: center;
    background-size: cover;
  }
  .content {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.2);
    background-color: white;
    opacity: 0.7;
    position: absolute;
    .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }
    .subtitle {
      font-weight: lighter;
      font-size: 16px;
      color: #2c2b2b;
    }
  }
}
